import React from 'react';

type LoadingProps = {
  message?: string;
  size?: 'small' | 'medium' | 'large';
};

export default function Loading({ 
  message = 'กำลังโหลดข้อมูล กรุณารอสักครู่...', 
  size = 'medium' 
}: LoadingProps) {
  // กำหนดขนาดตาม prop ที่ได้รับ
  const spinnerSize = {
    small: 'w-8 h-8',
    medium: 'w-16 h-16',
    large: 'w-24 h-24',
  }[size];

  const containerHeight = {
    small: 'min-h-[20vh]',
    medium: 'min-h-[40vh]',
    large: 'min-h-[60vh]',
  }[size];

  const textSize = {
    small: 'text-sm',
    medium: 'text-base',
    large: 'text-lg',
  }[size];

  return (
    <div className={`flex flex-col items-center justify-center ${containerHeight} bg-gray-50 rounded-lg shadow p-8`}>
      <div className="mb-6">
        <div className={`${spinnerSize} border-4 border-t-blue-500 border-blue-200 rounded-full animate-spin`}></div>
      </div>
      <h2 className={`${textSize} font-medium text-gray-800 mb-2`}>กำลังโหลดข้อมูล</h2>
      <p className="text-gray-600">{message}</p>
    </div>
  );
}

// สำหรับการแสดงข้อผิดพลาด
export function ErrorDisplay({ 
  message = 'ไม่สามารถโหลดข้อมูลได้', 
  details = 'ขออภัย ระบบไม่สามารถโหลดข้อมูลได้ในขณะนี้',
  size = 'medium'
}: {
  message?: string;
  details?: string;
  size?: 'small' | 'medium' | 'large';
}) {
  // กำหนดขนาดตาม prop ที่ได้รับ
  const iconSize = {
    small: 'w-8 h-8',
    medium: 'w-16 h-16',
    large: 'w-24 h-24',
  }[size];

  const containerHeight = {
    small: 'min-h-[20vh]',
    medium: 'min-h-[40vh]',
    large: 'min-h-[60vh]',
  }[size];

  const textSize = {
    small: 'text-sm',
    medium: 'text-base',
    large: 'text-lg',
  }[size];

  return (
    <div className={`flex flex-col items-center justify-center ${containerHeight} bg-gray-50 rounded-lg shadow p-8`}>
      <div className="text-red-500 mb-4">
        <svg xmlns="http://www.w3.org/2000/svg" className={iconSize} viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
          <circle cx="12" cy="12" r="10"></circle>
          <line x1="12" y1="8" x2="12" y2="12"></line>
          <line x1="12" y1="16" x2="12.01" y2="16"></line>
        </svg>
      </div>
      <h2 className={`text-xl font-semibold text-gray-800 mb-2`}>{message}</h2>
      <p className="text-gray-600 mb-4 text-center">{details}</p>
      <button 
        onClick={() => window.location.reload()} 
        className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600 transition-colors"
      >
        ลองใหม่อีกครั้ง
      </button>
    </div>
  );
}

// สำหรับการแสดงเมื่อไม่พบข้อมูล
export function EmptyData({
  title = 'ไม่พบข้อมูล',
  message = 'ไม่พบข้อมูลหรือไม่สามารถโหลดข้อมูลได้ในขณะนี้',
  size = 'medium'
}: {
  title?: string;
  message?: string;
  size?: 'small' | 'medium' | 'large';
}) {
  const iconSize = {
    small: 'w-8 h-8',
    medium: 'w-12 h-12',
    large: 'w-16 h-16',
  }[size];

  return (
    <div className="bg-gray-50 rounded-lg shadow p-6 mb-6">
      <div className="flex items-center justify-center flex-col py-6">
        <div className="text-gray-400 mb-4">
          <svg xmlns="http://www.w3.org/2000/svg" className={iconSize} viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
            <circle cx="12" cy="12" r="10"></circle>
            <line x1="8" y1="12" x2="16" y2="12"></line>
          </svg>
        </div>
        <h3 className="text-lg font-medium text-gray-600">{title}</h3>
        <p className="text-gray-500 mt-2 text-center">{message}</p>
      </div>
    </div>
  );
}